import {debounce} from 'lodash';

import React, {Component} from 'react';

class PeekAttributes extends Component {
    state = {
        block: undefined
    }

    componentDidMount() {
        const update  = (e) => {
            const el = document.elementFromPoint(e.pageX, e.pageY);
            const attr = el.getAttribute('data-block');
            console.log(attr);
            console.log(el)
            if(attr) {
                const block = wp.data.select( 'core/block-editor' ).getBlocksByClientId(attr)[0];
                this.setState({block: block} );
            } else {
                this.setState({block: {}});
            }

          
        }

        const debouncedMM = debounce(update, 300);

        document.addEventListener('mousemove', debouncedMM);
    }
    render() {
        return (
            <div>{block && JSON.stringify(this.state.block)}</div>
        )
    }
}

export default PeekAttributes;